import { ReactNode } from 'react';
import { Link } from 'react-router-dom';
import {
  Box,
  Flex,
  Avatar,
  HStack,
  
  IconButton,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  useDisclosure,
  useColorModeValue,
  Stack,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';

// const Links = ['Home', 'About', 'Skill','Projects','Contact'];

const NavLink = ({ children }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
      bg: useColorModeValue('gray.200', 'gray.700'),
    }}
    href={'#'}>
    {children}
  </Link>
);

export default function Simple() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <>
      <Box px={4} bg={"black"} fontWeight='bold' position="fixed" width="100%">
        <Flex h={16} alignItems={'center'} justifyContent={'space-between'}  direction={"right"}>
          <IconButton
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none' }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={"700px"} alignItems={'center'}>
            <Box fontSize='xl'>Rashmi Gautam</Box>
            <HStack
              as={'nav'}
              spacing={10}
              fontSize='xl'
              bg={"black"}
              display={{ base: 'none', md: 'flex' }}>
              {/* {Links.map((link) => (
                <NavLink  key={link}>{link}</NavLink>
              ))} */}
               <Link to="/">Home</Link>
               <Link to="/about">About</Link>
               <Link to="/skills">Skills</Link>
               <Link to="/project">Projects</Link>
               <Link to="/contact">Contact</Link>
            </HStack>
          </HStack>
          <Flex alignItems={'center'}>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: 'none' }}>
            <Stack as={'nav'} spacing={4} fontSize='xl'>
              {/* {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))} */}
             <Link to="/">Home</Link>
               <Link to="/about">About</Link>
               <Link to="/skills">Skills</Link>
               <Link to="/project">Projects</Link>
               <Link to="/contact">Contact</Link>
            </Stack>
          </Box>
        ) : null}
      </Box>
    </>
  );
}