import React from 'react'
import "../style/home.css"
import { Grid, GridItem ,Image,Text} from '@chakra-ui/react'
import rashmiProfile from "../rashmiProfile.jpeg"
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();


const Home = () => {
 

  return (
    <Grid templateColumns='repeat(5, 1fr)' gap={4} pt="100px">
      
    <GridItem colSpan={2} h='10' color={"white"} >
    <Text fontSize='6xl'>👋 Hello</Text>
    <div data-aos="fade-up">My name is Rashmi Gautam. I am a full stack web developer.</div>
    
    </GridItem>
    <GridItem colStart={4} colEnd={6} h='10'  >
    <Image src={rashmiProfile} alt="Rashmi" className='img-boxes' width="300px" id="image" />
    </GridItem>
  </Grid>
  )
}

export default Home
